import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { PendientesService } from '../../services/pendientes.service';


@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})
export class Tab1Page {


  constructor(public pendientesService:PendientesService, private router:Router, private alert:AlertController) {
  }

  async addLista(){
    const alert = await this.alert.create({
      cssClass: 'my-custom-class',
      header: 'Nueva Lista',
     inputs: [
       {
         name: 'titulo',
         type: 'text',
         placeholder: 'Nombre de la lista'
       }
      ],
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
        }, 
        {
            text: 'OK',
            handler: ( data ) =>{
              console.log(data);
              if(data.titulo.length === 0){
                return
              }

              const Listaid = this.pendientesService.crearLista( data.titulo )
              this.router.navigateByUrl(`/tabs/tab1/agregar/${Listaid}`);
            }
        }
    ]
    });

    alert.present();

  }

}
